<script setup lang="ts">
console.log('asdf')
    const props = defineProps({
        cpu: {
            type: Array,
            required: true
        },
        message: {
            type: Object,
            required: true
        }
    });

    /* Private variables */
    const _isLoading = ref<boolean>(true);
    const _isMounted = ref<boolean>(false);

    const m = ref<any>(props.message);

    function personalize(str: string): string {
        return str.replace('cpu_0', props.cpu[0].name).replace('user_name', 'Cory');
    }

    function isUser(str: string): boolean {
        return str === 'user_name';
    }

    function init(): void {
        setTimeout(() => {
            _isMounted.value = true;
        }, 1);
        if (isUser(m.value.from)) {
            _isLoading.value = false;
            return;
        }
        setTimeout(() => {
            _isLoading.value = false;
        }, 1000);
    }

    init();
</script>

<template>
        <v-expand-transition class="w-100">
            <div v-show="_isMounted">
    <v-row no-gutters
        class="ga-3 ma-3 mt-0"
        :class="`${isUser(m.from) ? 'justify-end' : 'justify-start'}`">
                <div style="max-width: 75%; position: relative;">
                    <div
                        style="border: 16px solid transparent; position: absolute;"
                        :style="isUser(m.from) ? 'border-bottom: 16px solid rgb(var(--v-theme-secondary)); right: -16px; bottom: 0;' : 'border-top: 16px solid rgb(var(--v-theme-primary)); left: -16px; top: 0;'"></div>                        
                    <v-sheet
                        class="px-3 py-1 rounded-lg"
                        :class="isUser(m.from) ? 'rounded-be-0' : 'rounded-ts-0'"
                        :color="isUser(m.from) ? 'secondary' : 'primary'"
                        elevation="8">
                        <v-expand-transition>
                            <div v-show="!_isLoading">
                                <span>
                                    {{ personalize(m.text) }}
                                </span>
                            </div>
                        </v-expand-transition>
                        <v-expand-transition>
                            <div v-show="_isLoading">
                                <span
                                    class="d-flex justify-center align-center">
                                    &nbsp;
                                    <v-progress-circular
                                        indeterminate
                                        size="16"
                                        width="2" />
                                    &nbsp;
                                </span>
                            </div>
                        </v-expand-transition>
                    </v-sheet>
                </div>
    </v-row>
            </div>
        </v-expand-transition>
</template>