<script setup lang="ts">
    definePageMeta({
        layout: 'no-nav',
        middleware: ['auth']
    });

    /* UI references */
    const actTextPractice = ref<HTMLElement>();

    function openTextPractice(): void {
        if (!actTextPractice.value) return;

        const dialog = <any>actTextPractice.value;
        dialog.start();
    }
</script>

<template>
    <div class="d-flex justify-center align-center mt-16">
        <v-btn @click="openTextPractice">Text Message Practice: Colors</v-btn>
        <ActTextPracticeLayout ref="actTextPractice" />
    </div>
</template>