<script setup lang="ts">
    import data from '~/data/text_message/practice/colors.json';
    import { useGoTo } from 'vuetify';


    const goTo = useGoTo();

    /* Private variables */
    const messages = ref<Array<any>>([]);
    const _data = ref<any>({});
    const _mode = ref<number>(0);
    const _isOver = ref<boolean>(false);
    const _question = ref<any>({});
    const _questionCnt = ref<number>(data.questions.length);
    const _correctCnt = ref<number>(0);
    const _options = ref<Array<any>>([]);
    
    const t = ref<boolean>(false);
    const _isInputMode = computed(() => _mode.value === 1);

    /* UI references */
    const chatArea = ref<HTMLElement>();

    
    function copyData(obj: any): any {
        return JSON.parse(JSON.stringify(obj));
    }

    function start(): void {
        _isOver.value = false
        t.value = true;
        messages.value = [];
        _data.value = copyData(data);
        _data.value.questions = shuffle(_data.value.questions);
        _questionCnt.value = _data.value.questions.length;
        nextMessage(250);
    }

    function nextMessage(delay: number = 1250): void {
        _mode.value = 0;
        setTimeout(() => {
            if (!chatArea.value) return;

            if (_data.value.opening.messages.length > 0) {
                messages.value.push(_data.value.opening.messages.shift());
            }
            else if (_data.value.questions.length > 0) {
                _question.value = _data.value.questions.shift();
                if (_data.value.fillers) _question.value.text = _data.value.fillers[Math.floor(Math.random() * _data.value.fillers.length)].replace('FILLER', _question.value.text);
                messages.value.push(_question.value);
                setTimeout(() => {
                    inputMode();
                }, 1000);
                scroll();
                return;
            }
            else if (_data.value.closing.messages.length > 0) {
                const closingMsg = _data.value.closing.messages.shift()
                closingMsg.text = closingMsg?.text.replace('CORRECT_CNT', _correctCnt.value.toString()).replace('TOTAL_CNT', _questionCnt.value.toString())
                messages.value.push(closingMsg);
            }
            else {
                _isOver.value = true;
                return;
            }
            scroll();
            nextMessage();
        }, delay);
    }

    function inputMode(): void {
        let allOptions = [ ..._question.value.options ];
        _options.value = allOptions.splice(_question.value.answer, 1);

        allOptions = shuffle(allOptions)
        _options.value = shuffle(_options.value.concat(allOptions.splice(0, 3)));

        _mode.value = 1;
    }

    function scroll(): void {
        console.log(`scroll to msg-${messages.value.length - 1}`)
        setTimeout(() => {
            goTo(10000000, { container: '#chatArea'})
            //window.scrollTo({ left: 0, top: document.documentElement.scrollHeight, behavior: 'smooth' })
        }, 1);
    }

    function selectOption(index: number): void {
        _mode.value = 0;
        messages.value.push({ from: 'user_name', text: _options.value[index] });
        scroll();
        setTimeout(() => {
            checkAnswer(_options.value[index]);
        }, 250);
    }

    function checkAnswer(res: string): void {
        let allOptions = [ ..._question.value.options ];
        const isCorrect = allOptions.indexOf(res) === _question.value.answer;
        if (isCorrect && _question.value.response < 0) {
            _correctCnt.value++;
        }
        _question.value.response = 2;
        const resPool = isCorrect ? data.responses.correct : data.responses.incorrect;
        messages.value.push({ from: 'cpu_0', text: resPool[Math.floor(Math.random() * resPool.length)] });
        scroll();
        setTimeout(() => {
            if (isCorrect) {
                nextMessage();
            }
            else {
                messages.value.push(_question.value);
                setTimeout(() => {
                    inputMode();
                }, 1000);
                scroll();
            }
        }, 1500)
    }

    function shuffle(arr: Array<any>): Array<any> {
        //return arr.sort((a, b) => a.title > b.title);
        let index = arr.length;
        while (index != 0) {
            let randomIndex = Math.floor(Math.random() * index);
            index--;
            [arr[index], arr[randomIndex]] = [arr[randomIndex], arr[index]];
        }
        return arr;
    }

    function close(): void {
        t.value = false;
    }

    defineExpose({
        start
    });
</script>

<template>
    <v-dialog fullscreen v-model="t"
          transition="dialog-bottom-transition">
        <template v-slot:default="{ isActive }">
            <v-card v-bind="isActive"
                color="white">
                <v-toolbar
                    class="px-3"
                    elevation="8">
                    <template #prepend>
                        <v-avatar
                            class="ml-0 mr-3">
                            <v-img
                                :alt="_data.cpu[0].name"
                                color="primary-lighten-3"
                                src="./cpu_0.png" />
                        </v-avatar>
                    </template>
                    <p>{{ _data.cpu[0].name }}</p>
                    <v-spacer />
                    <p>{{ _data.title }}</p>
                </v-toolbar>
                <div
                    class="w-100"
                    style="flex-grow: 1; overflow-y: auto; position: relative; justify-content: center; display:flex;">
                    <v-container ref="chatArea"
                        id="chatArea"
                        class="mx-auto py-3"
                        max-width="800"
                        style="position: absolute; bottom: 0;'">
                        <ActTextMessage v-for="(m, i) in messages" :key="i"
                            :id="`msg-${i}`"
                            :cpu="_data.cpu" :message="{ ...m }" />
                            <v-expand-transition>
                                <div v-show="_isOver">
                                    <v-row no-gutters class="justify-center mb-16">
                                        <v-btn @click="close">Close</v-btn>
                                    </v-row>
                                </div>
                            </v-expand-transition>
                    </v-container>
                </div>
                <v-expand-transition>
                    <div v-show="_isInputMode">
                        <v-sheet class="pa-1">
                            <v-row v-for="(o, i) in _options" :key="i"
                                no-gutters
                                class="mb-2">
                                <v-btn
                                    class="w-100 py-3"
                                    color="secondary"
                                    height="fit-content"
                                    @click="selectOption(i)">
                                    {{ o }}
                                </v-btn>
                            </v-row>
                        </v-sheet>
                    </div>
                </v-expand-transition>
            </v-card>
        </template>
    </v-dialog>
</template>